.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  overflow-x: hidden;
}

.inputs-list {
  display: flex;
  flex-direction: column;
}

.input-line {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 10px 0 20px 0;
}

.negative-balance .MuiInputBase-root {
  color: red;
}

.negative-balance .MuiFormHelperText-root {
  color: red;
}

.name-wrapper {
  width: 100%;
  max-width: 300px;
  height: 75px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 10px 0 0 0;
}

.name {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: 300;
  font-size: 28px;
  line-height: 33px;
  color: #000000;
}
